.app {
  position: relative;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #222222;
}
.app.app-body {
  width: 100vw;
  height: 100vh;
}
.app p,
.app h1,
.app h2,
.app h3,
.app h4,
.app h5,
.app h6 {
  word-wrap: break-word;
  display: block;
}
.app h1,
.app h2,
.app h3,
.app h4 {
  margin: 0px;
  padding: 0px;
}
.app .display-4 {
  font-size: 112px;
  font-weight: 300;
  line-height: 120px;
}
.app .display-3 {
  font-size: 56px;
  font-weight: 400;
  line-height: 64px;
}
.app .display-2 {
  font-size: 45px;
  font-weight: 400;
  line-height: 48px;
}
.app h1 {
  font-size: 34px;
  font-weight: 400;
  line-height: 40px;
}
.app h2 {
  font-size: 24px;
  font-weight: 400;
  line-height: 32px;
}
.app h3 {
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
}
.app h4 {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}
.app .subhead-2 {
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
}
.app caption {
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
}
.page {
  display: flex;
  flex-flow: column;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
}
.view {
  display: flex;
  flex-flow: column;
  flex-grow: 1;
  flex-shrink: 1;
}
.view.view-scrollable {
  overflow: auto;
}
.view.view-scrollable-y {
  overflow-y: auto;
}
.view.view-scrollable-x {
  overflow-x: auto;
}
.bar {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  flex-grow: 0;
  flex-shrink: 0;
  position: relative;
  min-height: 38px;
  padding: 5px;
  background: #f3f3f3;
  color: #222222;
}
.bar > div {
  display: flex;
  flex-flow: row;
  justify-content: center;
  align-items: center;
  flex-grow: 0;
  flex-shrink: 0;
  position: absolute;
  top: 5px;
  bottom: 5px;
}
.bar > div.left {
  left: 5px;
}
.bar > div.right {
  right: 5px;
}
.bar.bar-primary {
  background: #2980b9;
  color: #ffffff;
}
.bar.bar-secondary {
  background: #2c3e50;
  color: #ffffff;
}
.bar.bar-success {
  background: #27ae60;
  color: #ffffff;
}
.bar.bar-danger {
  background: #e74c3c;
  color: #ffffff;
}
.bar.bar-warning {
  background: #f39c12;
  color: #ffffff;
}
.bar.bar-info {
  background: #2980b9;
  color: #ffffff;
}
